/* ==========================================================================
# Search covers header - start
========================================================================== */

.edgt-search-cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: none;
    color: #fff;
    background-color: #fff;
    z-index: 115;

    //.edgt-boxed.edgt-sticky-header-appear & {
    //        width: 1150px;
    //        left: auto;
    //}


    
    .edgt-fixed-header-appear &,
    .edgt-sticky-header-appear &,
    .edgt-mobile-header.mobile-header-appear & {
	    position: fixed;
    }

    .edgt-fixed-header-appear &{
        border-bottom: 1px solid #e5e5e5;
    }

    .edgt-sticky-header-appear &{
        border-bottom: 1px solid #e5e5e5;
    }
	
	.edgt-fixed-header-appear.admin-bar &,
	.edgt-sticky-header-appear.admin-bar &,
    .admin-bar .edgt-mobile-header.mobile-header-appear & {
		top: 32px;
	}
    
    .edgt-form-holder-outer {
        display: table;
        width: 100%;
        height: 100%;
    }
    
    .edgt-form-holder {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        padding: 0 90px 0 60px;
    }
    
    .edgt-form-holder-inner {
        @include edgtRelativeHolderLayout();
        width: 96.5%;
    }

    .edgt-container {
        height: 100%;
        .edgt-container-inner {
            height: 100%;

            .edgt-form-holder {
                padding: 0;
            }

            .edgt-search-close {
                right: 45px;
            }
        }
    }
    
    input {
        display: inline-block;
        vertical-align: middle;
        width: 90%;
        margin: 0;
        padding: 0;
        float: left;
        font-family: inherit;
        color: #000;
        font-size: inherit;
        font-weight: inherit;
        background-color: transparent;
        border: 0;
        box-shadow: none;
        outline: none;
        outline-offset: 0;
        border-radius: 0;
        
        @include edgtPlaceholder {
            color: $default-text-color;
        }
    }
    
    input[type="submit"] {
        border: 0 none;
        clip: rect(0px,0px,0px,0px);
        height: 1px;
        margin: -1px;
        padding: 0;
        position: absolute;
        width: 1px;
        visibility: hidden;
    }
    
    .edgt-search-close {
        position: absolute;
        right: 0;
        top: 50%;
        font-size: 14px;
        line-height: 1em;
        @include edgtTransform(translateY(-50%));
    }
    
    .edgt-search-close {
        
        a {
            color: #000;
            display: inline-block;
            vertical-align: middle;
            
            * {
                display: block;
                line-height: inherit;
            }
            
            &:hover {
                color: $first-main-color;
            }
        }
    }
}

.edgt-search-cover.disabled {
    
    input,
    input:focus {
        font-weight: normal;
    }
}

/* ==========================================================================
# Search covers header - end
========================================================================== */