/* ==========================================================================
# Search opener - begin
========================================================================== */

.edgt-search-opener {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    font-size: 16px;
    color: #212121;
    line-height: 1;
    margin: 0 3px 0 12px;

    &:hover {
        color: #212121;
    }

    .edgt-search-opener-wrapper {
    
        > * {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            line-height: inherit;
        
            &:before {
                display: block;
                line-height: inherit;
            }
        }
    }
}

.edgt-light-header .edgt-page-header > div:not(.edgt-sticky-header):not(.fixed) .edgt-search-opener,
.edgt-light-header .edgt-top-bar .edgt-search-opener {
    color: $header-light-color !important; //Color can be set inline from widget opener

    &:hover {
        color: $header-light-hover-color !important;
    }
}

.edgt-dark-header .edgt-page-header > div:not(.edgt-sticky-header):not(.fixed) .edgt-search-opener,
.edgt-dark-header .edgt-top-bar .edgt-search-opener {
    color: $header-dark-color !important; //Color can be set inline from widget opener

    &:hover {
        color: $header-dark-hover-color !important;
    }
}
/* ==========================================================================
# Search opener - end
========================================================================== */